<template>
  <div class="choice">
    <back-bar :title="title"></back-bar>
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="option1" @change="changeValue" />
      <van-dropdown-item v-model="value2" :options="option2" @change="changeValue" />
    </van-dropdown-menu>
    <div class="products">
      <div class="product-item" v-for="(product,index) in products" :key="index">
        <img :src="product.imgUrl" />
        <div class="product-con">
          <h3>{{product.productName}}</h3>
          <p class="des">{{product.productDes}}</p>
          <p class="price">
            <span v-if="product.newPrice" class="new-price">￥{{product.newPrice}}起</span>
            <span v-if="product.oldPrice" class="old-price">￥{{product.oldPrice}}</span>
          </p>
          <div class="buybtn" @click="addToCart(product)">加入购物车</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BackBar from "@/components/BackBar";
import { DropdownMenu, DropdownItem } from "vant";
export default {
  name: "choice",
  data() {
    return {
      title: "商品选择",
      value1: 0,
      value2: 0,
      option1: [
        { text: "全部商品", value: 0 },
        { text: "小米手机", value: 1 },
        { text: "小米电视", value: 2 },
        { text: "小米电脑", value: 3 }
      ],
      option2: [
        { text: "默认排序", value: 0 },
        { text: "价格升序", value: 1 },
        { text: "价格降序", value: 2 }
      ],
      oriProducts: [],
      products: []
    };
  },
  components: {
    BackBar,
    vanDropdownMenu: DropdownMenu,
    vanDropdownItem: DropdownItem
  },
  methods: {
    getAllProduct() {
      this.$axios.get("data/allproduct.json").then(
        res => {
          res.data.modular.forEach(item => {
            this.oriProducts.push(...item.list);
          });
          this.products = [...this.oriProducts];
        },
        res => {
          console.log("数据获取失败");
        }
      );
    },
    changeValue() {
      if (this.value1 == 0) {
        this.products = [...this.oriProducts];
      } else {
        this.products = this.oriProducts.filter(item => {
          return item.type == this.value1;
        });
      }
      if (this.value2 == 1) {
        this.products.sort((a, b) => {
          return a.newPrice - b.newPrice;
        });
      } else if (this.value2 == 2) {
        this.products.sort((a, b) => {
          return b.newPrice - a.newPrice;
        });
      }
    },
    addToCart(val) {
      this.$store.commit("addToCart", val);
    }
  },
  mounted() {
    this.getAllProduct();
    this.$store.commit("setCartInfo");
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.choice {
  .van-dropdown-menu {
    position: fixed;
    width: 100vw;
    top: 0.92rem;
    height: 1rem;
    /deep/.van-dropdown-menu__title {
      font-size: 0.3rem;
      line-height: 0.3rem;
    }
    /deep/.van-dropdown-menu__title::after {
      right: -0.2rem;
      margin-top: -0.12rem;
      border: 0.08rem solid;
      border-color: transparent transparent currentColor currentColor;
    }
    /deep/.van-dropdown-menu__title--active {
      color: @mainColor;
    }
    /deep/.van-dropdown-menu__title--active::after {
      margin-top: 0rem;
    }
    /deep/.van-dropdown-item__option--active {
      color: @mainColor;
    }
    /deep/.van-dropdown-item__option--active .van-dropdown-item__icon {
      color: @mainColor;
    }
    /deep/.van-cell {
      padding: 0.2rem 0.32rem;
      font-size: 0.28rem;
      line-height: 0.48rem;
    }
  }
  .products {
    width: 100vw;
    box-sizing: border-box;
    padding: 1.92rem 0.12rem 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .product-item {
      width: 50%;
      box-sizing: border-box;
      > img {
        display: block;
        margin-top: 0.12rem;
        width: 100%;
      }
      .product-con {
        box-sizing: border-box;
        width: 100%;
        padding: 0.12rem 0.16rem;
        text-align: center;
        h3 {
          .font(0.28rem, #333, 400, 0.28rem);
        }
        .des {
          padding-top: 0.12rem;
          .font(0.24rem, #9e9e9e, 400, 0.24rem);
        }
        .price {
          padding-top: 0.12rem;
          .font(0.28rem, #9e9e9e, 400, 0.32rem);
          .new-price {
            color: @mainColor;
          }
          .old-price {
            padding-left: 0.12rem;
            font-size: 0.24rem;
            text-decoration: line-through;
          }
        }
        .buybtn {
          width: 2rem;
          margin: 0.12rem auto;
          background: #ea625b;
          text-align: center;
          .font(0.24rem, #fff, 700, 0.56rem);
          border-radius: 0.06rem;
        }
      }
    }
    .product-item:nth-child(2n-1) {
      padding-right: 0.1rem;
    }
    .product-item:nth-child(2n) {
      padding-left: 0.1rem;
    }
  }
}
</style>