// 将state数据存到sessionStorage
export default {
  // 存储用户信息
  getUserInfo: _this => {
    let { userInfo } = _this.$store.state;
    if (localStorage.getItem('userInfo')) {
      userInfo = JSON.parse(localStorage.getItem('userInfo'));
    } else {
      localStorage.setItem('userInfo', JSON.stringify(userInfo));
    }
  },
  // 更新并且存储用户信息
  storeUserInfo: (_this, val) => {
    _this.$store.commit('addUserInfo', val)
    _this.common.getUserInfo(_this)
  },
  // 更新并存储登录用户
  storeLoginUser: (_this, val) => {
    _this.$store.commit('setLoginUser', val)
    localStorage.setItem('loginUser', val)
  }
}