<template>
  <div class="login">
    <back-bar :title="title"></back-bar>
    <van-cell-group>
      <van-field
        v-model="username"
        required
        clearable
        type="text"
        label="手机号"
        maxlength="11"
        left-icon="contact"
        placeholder="请输入手机号"
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        left-icon="bag-o"
        required
        clearable
      />
      <van-field
        v-model="password2"
        type="password"
        label="确认密码"
        placeholder="请再次输入密码"
        left-icon="bag-o"
        required
        clearable
      />
    </van-cell-group>
    <van-button type="primary" size="normal" :disabled="getFlag" @click="submitForm">注册</van-button>
  </div>
</template>

<script type="text/javascript">
import BackBar from "@/components/BackBar";
import { CellGroup, Field, Button, Toast } from "vant";
import { user, user2 } from "@/utils/index";
export default {
  data() {
    return {
      title: "登录",
      username: "",
      password: "",
      password2: ""
    };
  },
  components: {
    BackBar,
    vanCellGroup: CellGroup,
    vanField: Field,
    vanButton: Button
  },
  computed: {
    getFlag() {
      const { username, password, password2 } = this;
      return !(username != "" && password != "" && password2 != "");
    }
  },
  mounted() {
    // 清除注册用户
    localStorage.removeItem("loginUser");
  },
  methods: {
    testUser() {
      let reg = /^1[345678][0-9]{9}$/.test(this.username);
      if (!reg) {
        Toast("手机号格式不对");
      } else {
        const { userInfo } = this.$store.state;
        const flag = userInfo.some(({ username }) => {
          return this.username == username;
        });
        if (flag) {
          Toast("手机号已注册");
        } else {
          return true;
        }
      }
    },
    testPass() {
      return true;
    },
    testPass2() {
      if (this.password != this.password2) {
        Toast("两次密码不一致");
      } else {
        return true;
      }
    },
    submitForm() {
      if (this.testUser() && this.testPass() && this.testPass2()) {
        // 存储已注册用户
        this.common.getUserInfo(this);
        const { userInfo } = this.$store.state;
        // 出现已注册用户
        const flag = userInfo.some(item => {
          return item.name == this.username;
        });
        if (flag) {
          Toast("该手机号已注册");
        } else {
          Toast("恭喜你，注册成功！");
          const user = {
            name: this.username,
            pass: this.password
          };
          // 存入注册用户
          this.common.storeUserInfo(this, user);
          // 将注册用户标为登录用户
          this.common.storeLoginUser(this, user.name);
          const _this = this;
          // 跳转至我的页面
          setTimeout(function() {
            _this.$router.push("/my");
          }, 2000);
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";

.login {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  .van-cell-group {
    margin-top: 0.92rem;
  }
}
.van-field::before {
  content: "";
}
/deep/.van-field__left-icon .van-icon {
  font-size: 0.32rem;
}
/deep/.van-cell__value {
  position: relative;
  .van-field__error-message {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    font-size: 0.28rem;
  }
}
.van-cell {
  font-size: 0.28rem;
  line-height: 0.88rem;
  /deep/.van-field__label {
    width: 1.2rem;
    text-align: left;
  }
}
.van-button {
  font-size: 0.28rem;
  width: 6.8rem;
  height: 0.88rem;
  background-color: @mainColor;
  border-color: @mainColor;
  top: 6rem;
}
/deep/.van-icon-clear {
  font-size: 0.28rem;
}
</style>