<template>
  <div class="add-address">
    <back-bar :title="title" />
    <ul class="address">
      <li>
        <span>收货人:</span>
        <input type="text" placeholder="真实姓名" v-model="name" />
      </li>
      <li>
        <span>手机号码:</span>
        <input type="text" placeholder="手机号" v-model="tel" />
      </li>
      <li>
        <span>所在地区:</span>
        <!-- <p>省 市 区 街道信息</p> -->
        <input type="text" placeholder="省 市 区信息" readonly @click="showPopup" v-model="fullCity" />
      </li>
      <li>
        <span>详细地址:</span>
        <input type="text" placeholder="详细地址" v-model="detailed" />
      </li>
      <li>
        <span>设置为默认地址</span>
        <input type="checkbox" v-model="isDefault" />
      </li>
    </ul>
    <van-popup v-model="show" position="bottom" :style="{height:'5rem'}">
      <div class="title">
        <p>选择地址</p>
        <van-icon name="cross" @click="showPopup" />
      </div>
      <ul class="citys">
        <li class="province" :class="citysFlag==0?'active':''" @click="citysFlag=0">
          <span>{{province}}</span>
        </li>
        <li class="city" :class="citysFlag==1?'active':''" v-if="city!=''" @click="citysFlag=1">
          <span>{{city}}</span>
        </li>
        <li class="county" :class="citysFlag==2?'active':''" v-if="county!=''" @click="citysFlag=2">
          <span>{{county}}</span>
        </li>
      </ul>
      <ul class="citys-list">
        <li v-if="citysFlag==0">
          <ul class="citys-item">
            <li
              v-for="(item,index) in provinceList"
              :key="index"
              @click="choiceProvince(item)"
              :class="item==province?'active':''"
            >{{item}}</li>
          </ul>
        </li>
        <li v-if="citysFlag==1">
          <ul class="citys-item">
            <li
              v-for="(item,index) in cityList"
              :key="index"
              @click="choiceCity(item.name)"
              :class="item.name==city?'active':''"
            >{{item.name}}</li>
          </ul>
        </li>
        <li v-if="citysFlag==2">
          <ul class="citys-item">
            <li
              v-for="(item,index) in countyList"
              :key="index"
              @click="choiceCounty(item)"
              :class="item==county?'active':''"
            >{{item}}</li>
          </ul>
        </li>
      </ul>
    </van-popup>
    <div class="addBtn">
      <button @click="addAddress">确认添加</button>
    </div>
  </div>
</template>

<script type="text/javascript">
import BackBar from "@/components/BackBar";
import { Popup, Icon, Toast } from "vant";
export default {
  data() {
    return {
      title: "新增地址",
      areaList: [],
      provinceList: [],
      cityList: [],
      countyList: [],
      show: false,
      province: "请选择",
      city: "",
      county: "",
      detailed: "",
      citysFlag: 0,
      fullCity: "",
      name: "",
      tel: "",
      isDefault: false
    };
  },
  components: {
    BackBar,
    vanPopup: Popup,
    vanIcon: Icon
  },
  methods: {
    showPopup() {
      this.show = !this.show;
      this.citysFlag = 0;
    },
    choiceProvince(val) {
      Object.assign(this, {
        province: val,
        city: "请选择",
        fullCity: ""
      });
      this.citysFlag++;
    },
    choiceCity(val) {
      Object.assign(this, {
        city: val,
        county: "请选择",
        fullCity: ""
      });
      this.citysFlag++;
    },
    choiceCounty(val) {
      Object.assign(this, {
        county: val,
        fullCity: this.province + this.city + val,
        show: false
      });
    },
    addAddress() {
      if (
        this.name &&
        this.tel &&
        this.province &&
        this.city &&
        this.county &&
        this.detailed
      ) {
        const newAddress = {
          name: this.name,
          tel: this.tel,
          province: this.province,
          city: this.city,
          county: this.county,
          detailed: this.detailed,
          address: this.province + this.city + this.county + this.detailed,
          isDefault: this.isDefault
        };
        this.$store.commit("addAddressInfo", newAddress);
        this.$router.go(-1);
      } else {
        Toast("请完善信息");
      }
    }
  },
  mounted() {
    this.$axios.get("data/citylist.json").then(
      res => {
        // let { areaList, provinceList, cityList, countList } = this;
        Object.assign(this, {
          areaList: res.data.citysList
        });
        this.areaList.forEach(({ name }) => {
          this.provinceList.push(name);
        });
      },
      res => {
        console.log("获取失败");
      }
    );
  },
  watch: {
    province: function(val) {
      this.areaList.forEach(({ name, city }) => {
        if (name == val) {
          this.cityList = city;
        }
      });
    },
    city: function(val) {
      this.cityList.forEach(({ name, area }) => {
        if (name == val) {
          this.countyList = area;
        }
      });
    }
  },
  computed: {
    isDisabled() {}
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.add-address {
  .address {
    padding-top: 1rem;
    li {
      line-height: 1rem;
      .font(0.28rem, #3c3c3c, 400, 0.6rem);
      padding: 0.2rem 0.3rem;
      border-bottom: 1px solid #f6f6f6;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      span {
        flex: 1;
        text-align: left;
      }
      input {
        flex: 4;
        color: #3c3c3c;
      }
    }
    li:last-child {
      justify-content: space-between;
      input {
        flex: 0;
        font-size: 0.4rem;
      }
    }
  }
  .van-popup {
    .title {
      padding: 0 0.3rem;
      display: flex;
      justify-content: space-between;
      .font(0.32rem, #3c3c3c, 400, 0.8rem);
      align-items: center;
      p {
        font-weight: 500;
      }
    }
    .citys {
      display: flex;
      justify-content: flex-start;
      border-bottom: 1px solid #f6f6f6;
      .font(0.28rem, #3c3c3c, 500, 0.8rem);
      .province,
      .city,
      .county {
        padding-left: 0.3rem;
        span {
          text-overflow: ellipsis;
          white-space: nowrap;
          display: inline-block;
          line-height: 0.8rem;
        }
      }
      .active {
        span {
          border-bottom: 1px solid @mainColor;
        }
      }
    }
    .citys-list {
      .font(0.28rem, #3c3c3c, 400, 0.8rem);
      text-align: left;
      padding-left: 0.4rem;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 1.8rem;
      overflow-y: scroll;
      .citys-item {
        .active {
          color: @mainColor;
        }
      }
    }
  }
  .addBtn {
    width: 100%;
    height: 1rem;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 0.1rem;
    button {
      width: 6.86rem;
      background: #ff6034;
      border-radius: 0.5rem;
      .font(0.32rem, #fff, 500, 1rem);
      border: none;
    }
  }
}
</style>