<template>
  <div class="address">
    <back-bar :title="title" />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      :disabled-list="disabledList"
      disabled-text="以下地址超出配送范围"
      default-tag-text="默认"
      @add="onAdd"
      @edit="onEdit"
      @select="onSelect"
    />
  </div>
</template>

<script>
import BackBar from "@/components/BackBar";
import { AddressList, Toast } from "vant";
export default {
  data() {
    return {
      title: "我的地址",
      chosenAddressId: 1001,
      list: [
        // {
        //   id: "1",
        //   name: "张三",
        //   tel: "13000000000",
        //   address: "浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室",
        //   isDefault: true
        // }
      ],
      disabledList: []
    };
  },
  components: {
    BackBar,
    vanAddressList: AddressList
  },
  methods: {
    onAdd() {
      // Toast("新增地址");
      this.$router.push("/addaddress");
    },
    onEdit(item, index) {
      Toast("编辑地址:" + index);
    },
    onSelect(item) {
      this.$store.commit("setCheckedAddress", item);
    }
  },
  mounted() {
    this.list = JSON.parse(localStorage.getItem("addressInfo"));
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.van-address-list {
  padding-top: 1rem;
  /deep/.van-radio__icon--checked {
    .van-icon-success {
      background-color: @mainColor;
      border-color: @mainColor;
    }
  }
  .van-address-item {
    padding: 0.32rem;
    .van-address-item__value {
      padding-right: 0.32rem;
      /deep/.van-radio__icon {
        font-size: 0.32rem !important;
      }
      /deep/.van-radio__label {
        margin-left: 0.16rem;
        line-height: 0.4rem;
        .van-address-item__name {
          margin-bottom: 0.08rem;
          font-size: 0.28rem;
          line-height: 0.4rem;
        }
        .van-address-item__address {
          font-size: 0.24rem;
          line-height: 0.32rem;
        }
      }
    }
    .van-address-item__edit {
      right: 0.32rem;
      font-size: 0.32rem;
    }
  }
  .van-address-list__add {
    background: @mainColor;
    border-color: @mainColor;
    height: 1rem;
    line-height: 1rem;
    font-size: 0.32rem;
  }
  .van-address-list__disabled-text {
    display: none;
  }
}
</style>