<template>
  <div class="classify">
    <title-bar :title="title" />
    <div class="list-box">
      <ul class="list-navbar">
        <li
          class="navbar-itme"
          v-for="(navItem,index) in classify"
          :key="index"
          :class="index==curIndex?'active':''"
          @click="getScroll(index)"
        >
          <span>{{navItem.category_name}}</span>
        </li>
      </ul>
      <div class="list-con">
        <div class="list-item" v-for="(listItem,index) in classify" :key="index">
          <div class="list-item-main">
            <div
              v-for="(listItem,ind) in listItem.category_list"
              :key="ind"
              :class="listItem.view_type"
            >
              <img
                v-if="listItem.view_type=='cells_auto_fill'"
                :src="listItem.body.items[0].img_url"
              />
              <span v-if="listItem.view_type=='category_title'">{{listItem.body.category_name}}</span>
              <ul v-if="listItem.view_type=='category_group'" class="group_item">
                <li v-for="(product,proIndex) in listItem.body.items" :key="proIndex">
                  <img :src="product.img_url" />
                  <span>{{product.product_name}}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <tab-bar :active="active"></tab-bar>
  </div>
</template>

<script type="text/javascript">
import TitleBar from "@/components/TitleBar";
import TabBar from "@/components/TabBar";
export default {
  data() {
    return {
      title: "分类",
      active: 1,
      classify: [],
      curIndex: 0,
      curScroll: []
    };
  },
  components: {
    TabBar,
    TitleBar
  },
  mounted() {
    this.$axios.get("data/classify.json").then(
      res => {
        this.classify = res.data.product;
      },
      res => {
        console.log("error");
      }
    );
    setTimeout(() => {
      document.querySelectorAll(".list-item").forEach(item => {
        this.curScroll.push(item.getBoundingClientRect().top);
      });
      window.onscroll = () => {
        let top =
          document.scrollingElement.scrollTop || document.body.scrollTop;
        this.curScroll.forEach((item, index) => {
          if (item <= top && top < this.curScroll[index + 1]) {
            this.curIndex = index;
          }
        });
      };
    }, 1000);
  },
  methods: {
    getScroll(val) {
      this.curIndex = val;
      document.scrollingElement.scrollTop = this.curScroll[val];
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.classify {
  .list-box {
    margin-top: 1rem;
    display: flex;
    justify-content: flex-start;
    background: #fff;
    .list-navbar {
      z-index: 2;
      position: fixed;
      top: 1rem;
      width: 1.52rem;
      height: 11.34rem;
      height: 100%;
      box-sizing: border-box;
      padding: 0.3rem 0;
      border-right: 0.02rem solid #efefef;
      overflow: hidden;
      overflow-y: scroll;
      li {
        height: 0.9rem;
        padding: 0 0.12rem;
        .font(0.28rem, #3c3c3c, 400, 0.9rem);
        overflow: hidden;
      }
      li.active {
        color: @mainColor;
        font-size: 0.32rem;
        padding: 0;
        font-weight: 500;
      }
    }
    .list-navbar::-webkit-scrollbar {
      display: none;
    }
    .list-con {
      margin-left: 1.52rem;
      top: 1rem;
      width: 5.98rem;
      overflow: hidden;
      overflow-y: scroll;
      box-sizing: border-box;
      padding: 0 0.24rem;
      .cells_auto_fill {
        img {
          display: block;
          width: 100%;
        }
      }
      .category_title {
        padding-top: 0.2rem;
        text-align: center;
        span {
          .font(0.28rem, #3c3c3c, 400, 1.28rem);
          position: relative;
        }
        span::before {
          content: "";
          display: block;
          position: absolute;
          left: -0.6rem;
          top: 50%;
          width: 0.4rem;
          height: 1px;
          background: #e0e0e0;
        }
        span::after {
          content: "";
          display: block;
          position: absolute;
          right: -0.6rem;
          top: 50%;
          width: 0.4rem;
          height: 1px;
          background: #e0e0e0;
        }
      }
      .category_group {
        .group_item {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
          li {
            width: 33.33%;
            display: flex;
            flex-direction: column;
            overflow: hidden;
            text-align: center;
            margin: 0.2rem 0 0.3rem;
            img {
              margin: 0 auto;
              width: 1rem;
            }
            span {
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              padding-top: 0.24rem;
              .font(0.24rem, #6e6e6e, 400, 0.24rem);
            }
          }
        }
      }
    }
  }
}
</style>