<template>
  <div class="shopcar">
    <title-bar :title="title" />
    <div class="shopcar-empty" v-if="products.length==0">
      <div class="go-login" @click="goLogin" v-if="!loginState">
        <p>登录后享受更多优惠</p>
        <p>
          <span>去登录</span>
          <van-icon name="arrow" />
        </p>
      </div>
      <div class="go-home">
        <img src="https://m.mi.com/static/img/cartnull.daaf7926f8.png" />
        <span>购物车还是空的</span>
        <button @click="goHome">去逛逛</button>
      </div>
    </div>
    <ul class="shopcar-con" v-else>
      <li class="product-item" v-for="(item,index) in products" :key="index">
        <div class="choice" :class="item.checked?'actived':''" @click="choiceOne(item)">
          <van-icon name="success" v-show="item.checked" />
        </div>
        <img :src="item.imgUrl" />
        <div class="info-con">
          <div class="info">
            <p class="title">
              {{item.productName}}&nbsp;&nbsp;&nbsp;
              {{item.productDes}}
            </p>
            <p class="price">售价：{{item.newPrice}}元</p>
          </div>
          <div class="btns">
            <van-stepper
              v-model="item.num"
              @plus="choiceStepper(item,1)"
              @minus="choiceStepper(item,0)"
              max="10"
              integer
            ></van-stepper>
            <div class="delete" @click="deleteOne(item,index)">
              <van-icon name="delete" />
            </div>
          </div>
        </div>
      </li>
    </ul>
    <tab-bar :active="active" v-if="products.length==0"></tab-bar>
    <div class="submit-bar" v-else>
      <div class="choice-all" @click="choiceAll">
        <div class="choice" :class="isChoiceAll?'actived':''">
          <van-icon name="success" v-show="isChoiceAll" />
        </div>
        <span>{{isChoiceAll?'取消全选':'全选'}}</span>
      </div>
      <div class="price-box">
        <span class="total-num">共{{allNum}}件&nbsp;金额:</span>
        <span class="total-price">
          {{totalPrice}}
          <i>元</i>
        </span>
      </div>
      <div class="submit-btn" @click="goCheckout">立即购买</div>
    </div>
  </div>
</template>

<script type="text/javascript">
import TabBar from "@/components/TabBar";
import TitleBar from "@/components/TitleBar";
import { Icon, Stepper, Toast } from "vant";
export default {
  data() {
    return {
      active: 2,
      title: "购物车",
      products: [],
      isChoiceAll: false,
      allNum: 0,
      totalPrice: 0,
      loginState: false
    };
  },
  components: {
    TabBar,
    TitleBar,
    vanIcon: Icon,
    vanStepper: Stepper
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
    goLogin() {
      this.$router.push("/login");
    },
    goCheckout() {
      const flag = this.products.every(item => {
        return item.checked == false;
      });
      if (flag) {
        Toast("您还没有购买任何一件商品");
      } else {
        // 筛选未购买的商品
        const data = this.products.filter(item => {
          return item.checked == false;
        });
        const payData = this.products.filter(item => {
          return item.checked == true;
        });
        localStorage.setItem("cartInfo", JSON.stringify(data));
        // 存储已购买的商品
        this.$store.commit("setPayCartInfo", payData);
        this.$router.push("/checkout");
      }
    },
    // 获取数据
    getAllProduct() {
      const cartInfo = JSON.parse(localStorage.getItem("cartInfo"));
      this.products.push(...cartInfo);
    },

    // 单选
    choiceOne(el) {
      // 改变选择状态
      el.checked = !el.checked;
      this.getTotalPrice();
    },
    getTotalPrice() {
      this.totalPrice = 0;
      this.allNum = 0;
      this.products.forEach(item => {
        if (item.checked) {
          this.allNum += parseInt(item.num);
          this.totalPrice += item.newPrice * item.num;
        }
      });
    },
    choiceStepper(el, type) {
      if (el.checked) {
        if (type == 0) {
          this.allNum--;
          this.totalPrice -= el.newPrice;
        }
        if (type == 1) {
          this.allNum++;
          this.totalPrice += el.newPrice;
        }
      }
    },
    choiceAll() {
      this.isChoiceAll = !this.isChoiceAll;
      if (this.isChoiceAll) {
        this.products.forEach(item => {
          item.checked = true;
        });
        this.getTotalPrice();
      } else {
        this.products.forEach(item => {
          item.checked = false;
        });
        this.totalPrice = 0;
        this.allNum = 0;
      }
    },
    deleteOne(el, cur) {
      if (el.checked) {
        this.allNum -= el.num;
        this.totalPrice -= el.num * el.newPrice;
      }
      this.products.splice(cur, 1);
      localStorage.setItem("cartInfo", JSON.stringify(this.products));
    },
    getLoginState() {
      this.loginState = localStorage.getItem("loginUser") ? true : false;
    }
  },
  mounted() {
    this.getAllProduct();
    this.getLoginState();
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.shopcar {
  height: 100vh;
  background: #f5f5f5;
  .shopcar-empty {
    position: absolute;
    top: 1rem;
    bottom: 1rem;
    right: 0;
    left: 0;
    background: #f5f5f5;
    width: 100%;
    display: flex;
    flex-direction: column;
    .go-login {
      display: flex;
      justify-content: space-between;
      height: 1.04rem;
      background: #fff;
      padding: 0 0.32rem;
      .font(0.32rem, #333, 400, 1.04rem);
      p:last-child {
        .font(0.24rem, #666, 400, 1.04rem);
        display: flex;
        justify-content: flex-end;
        align-items: center;
        span {
          padding-right: 0.12rem;
        }
        .van-icon-arrow {
          font-size: 0.28rem;
        }
      }
    }
    .go-home {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      .font(0.24rem, #999, 400, 0.8rem);
      img {
        width: 0.8rem;
      }
      span {
        padding: 0 0.2rem 0;
      }
      button {
        background: none;
        border: 1px solid #999;
        height: 0.4rem;
        padding: 0 0.24rem;
        text-align: center;
        color: #666;
        line-height: 0.4rem;
      }
    }
  }
  .shopcar-con {
    position: absolute;
    top: 1rem;
    right: 0;
    left: 0;
    background: #f5f5f5;
    padding-bottom: 1.2rem;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #fff;
      margin-bottom: 0.2rem;
      padding: 0.28rem 0.12rem;
      .choice {
        width: 0.3rem;
        height: 0.3rem;
        border: 1px solid #999;
        border-radius: 50%;
        .van-icon-success {
          display: block;
          color: #fff;
          line-height: 0.3rem;
          font-size: 0.2rem;
        }
      }
      .choice.actived {
        border-color: @mainColor;
        background: @mainColor;
      }
      > img {
        width: 1.8rem;
        height: 1.8rem;
        border: 1px solid #eee;
        margin: 0 0.16rem;
      }
      .info-con {
        flex: 1;
        height: 1.8rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .info {
          text-align: left;
          .font(0.28rem, #666, 400, 0.4rem);
          .price {
            padding: 0.1rem 0;
            font-size: 0.22rem;
            color: #999;
          }
        }
        .btns {
          display: flex;
          justify-content: space-between;
          .van-stepper {
            /deep/button {
              border-radius: 0.04rem;
              width: 0.56rem;
              height: 0.56rem;
              padding: 0.08rem;
            }
            /deep/input {
              width: 0.64rem;
              height: 0.56rem;
              margin: 0 0.02rem;
              font-size: 0.28rem;
            }
          }
          .delete {
            width: 0.56rem;
            height: 0.56rem;
            .van-icon-delete {
              display: block;
              font-size: 0.44rem;
              line-height: 0.56rem;
              color: #999;
            }
          }
        }
      }
    }
  }
  .submit-bar {
    position: fixed;
    bottom: 0;
    width: 100vw;
    height: 1rem;
    box-shadow: 0 -0.04rem 0.08rem rgba(0, 0, 0, 0.1);
    background: #fff;
    box-sizing: border-box;
    padding-left: 0.12rem;
    display: flex;
    align-items: center;
    .choice-all {
      display: flex;
      justify-content: flex-start;
      .font(0.28rem, #666, 400, 1rem);
      align-items: center;
      .choice {
        width: 0.3rem;
        height: 0.3rem;
        border: 1px solid #999;
        border-radius: 50%;
        .van-icon-success {
          display: block;
          color: #fff;
          line-height: 0.3rem;
          font-size: 0.2rem;
        }
      }
      .choice.actived {
        border-color: @mainColor;
        background: @mainColor;
      }
      span {
        padding-left: 0.2rem;
      }
    }
    .price-box {
      display: flex;
      flex: 1;
      flex-direction: column;
      padding-left: 0.5rem;
      text-align: left;
      .total-num {
        padding-top: 0.2rem;
        .font(0.24rem, #666, 400, 0.3rem);
      }
      .total-price {
        .font(0.4rem, @mainColor, 600, 0.4rem);
        padding-bottom: 0.1rem;
        i {
          .font(0.24rem, #999, 600, 0.4rem);
        }
      }
    }
    .submit-btn {
      width: 2.5rem;
      .font(0.3rem, #fff, 500, 1rem);
      background: @mainColor;
    }
  }
}
</style>