<template>
  <van-nav-bar :title="title" left-text="返回" left-arrow @click-left="onClickLeft" />
</template>

<script type="text/javascript">
import { NavBar } from "vant";
export default {
  props: {
    title: {
      type: String,
      default: "标题"
    }
  },
  data() {
    return {};
  },
  components: {
    vanNavBar: NavBar
  },
  computed: {
    getTitle: {
      get: function() {
        return this.title;
      },
      set: function(val) {
        this.$emit("updata:title", val);
      }
    }
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";

.van-nav-bar {
  position: fixed;
  width: 100vw;
  height: 0.92rem;
  .van-nav-bar__left {
    .van-nav-bar__text {
      .font(0.28rem, @mainColor, 400, 0.92rem);
    }
    .van-icon {
      .font(0.28rem, @mainColor, 400, 0.92rem);
    }
  }
  .van-nav-bar__title {
    .font(0.32rem, #323232, 500, 0.92rem);
  }
}
</style>