<script type="text/javascript">
import TabBar from "@/components/TabBar";
import { Icon } from "vant";
export default {
  data() {
    return {
      active: 3,
      loginUser: ""
    };
  },
  components: {
    TabBar,
    vanIcon: Icon
  },
  methods: {
    goLogin() {
      this.$router.push("/login");
    },
    signOut() {
      localStorage.removeItem("loginUser");
      this.loginUser = "";
    }
  },
  computed: {
    watchLogin() {
      if (this.loginUser) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted() {
    this.loginUser = localStorage.getItem("loginUser");
  }
};
</script>

