<template>
  <div class="checkout">
    <back-bar :title="title" />
    <div class="ship-address" @click="goAddress">
      <div class="info">
        <p>
          <span>{{checkedAddress.name}}</span>
          <span>{{hideTel(checkedAddress.tel)}}</span>
        </p>
        <p>{{checkedAddress.province}} {{checkedAddress.city}} {{checkedAddress.county}} {{checkedAddress.detailed}}</p>
      </div>
      <div class="more">
        <van-icon name="arrow" />
      </div>
    </div>
    <!-- 支付方式 -->
    <van-radio-group v-model="payway">
      <van-cell-group>
        <van-cell title="小米钱包" clickable @click="payway = 'mipay'">
          <van-radio slot="right-icon" name="mipay" />
        </van-cell>
        <van-cell title="支付宝" clickable @click="payway = 'alipay'">
          <van-radio slot="right-icon" name="alipay" />
        </van-cell>
        <van-cell title="微信支付" clickable @click="payway = 'wepay'">
          <van-radio slot="right-icon" name="wepay" />
        </van-cell>
      </van-cell-group>
    </van-radio-group>

    <van-collapse v-model="activeNames">
      <van-collapse-item title="运费" name="postage" value="免邮" disabled></van-collapse-item>
      <van-collapse-item title="配送" name="time" class="time" :value="delivery[deliveryFlag]">
        <p class="time">送货时间</p>
        <ul class="time-list">
          <li
            :class="index==deliveryFlag?'active':''"
            v-for="(item,index) in delivery"
            :key="index"
            @click="deliveryFlag=index"
          >{{item}}</li>
        </ul>
      </van-collapse-item>
      <van-collapse-item title="电子发票" name="invoice" value="个人" disabled></van-collapse-item>
      <van-collapse-item title="优惠券" name="coupons" value="无" disabled></van-collapse-item>
    </van-collapse>

    <ul class="products">
      <li v-for="(item,index) in payCartInfo" :key="index">
        <img :src="item.imgUrl" />
        <p>
          {{item.productName}}
          <span>x{{item.num}}</span>
        </p>
        <span>{{item.newPrice}}</span>
      </li>
    </ul>
    <ul class="detail">
      <li>
        <span>商品价格：</span>
        {{totalPrice}}
      </li>
      <li>
        <span>优惠：</span>
        -0.00
      </li>
      <li>
        <span>配送费用：</span>
        0.00
      </li>
    </ul>
    <div class="payinfo">
      <p
        class="address"
      >配送至：{{checkedAddress.province}} {{checkedAddress.city}} {{checkedAddress.county}} {{checkedAddress.detailed}}</p>
      <div>
        <p class="price">
          共{{totalNum}}件 合计:
          <span>{{totalPrice}}</span>
        </p>
        <p class="btn">去付款</p>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import BackBar from "@/components/BackBar";
import {
  Icon,
  RadioGroup,
  Radio,
  CellGroup,
  Cell,
  Collapse,
  CollapseItem
} from "vant";
export default {
  data() {
    return {
      title: "用户结算",
      payway: "mipay",
      activeNames: ["postage"],
      delivery: ["不限送货时间", "工作日送货", "双休日、假日送货"],
      deliveryFlag: 0,
      payCartInfo: [],
      totalPrice: 0,
      totalNum: 0,
      checkedAddress: {}
    };
  },
  components: {
    BackBar,
    vanIcon: Icon,
    vanRadioGroup: RadioGroup,
    vanRadio: Radio,
    vanCellGroup: CellGroup,
    vanCell: Cell,
    vanCollapse: Collapse,
    vanCollapseItem: CollapseItem
  },
  methods: {
    goAddress() {
      this.$router.push("/address");
    },
    hideTel(val) {
      return val && val.replace(/(\d{3}).*(\d{4})/, "$1****$2");
    }
  },
  mounted() {
    // 获取已购买的商品信息
    this.payCartInfo = JSON.parse(localStorage.getItem("payCartInfo"));
    this.payCartInfo.forEach(item => {
      this.totalPrice += item.newPrice * item.num;
      this.totalNum += item.num;
    });
    const { addressInfo, checkedAddress } = this.$store.state;
    //预先获取地址
    if (localStorage.getItem("addressInfo") == null) {
      localStorage.setItem("addressInfo", JSON.stringify(addressInfo));
    }
    localStorage.setItem("checkedAddress", JSON.stringify(checkedAddress));
    this.checkedAddress = checkedAddress;
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.checkout {
  background: #f5f5f5;
  overflow: hidden;
  .ship-address {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.92rem;
    padding: 0.4rem 0.32rem;
    background: #fff;
    background-image: url(https://s1.mi.com/m/images/m/bd1.png);
    background-repeat: repeat-x;
    margin-bottom: 0.3rem;
    // border-bottom: 0.02rem solid #e7e7e7;
    p {
      text-align: left;
      .font(0.3rem, #333, 500, 0.4rem);
      span {
        padding-right: 0.1rem;
      }
    }
    p:first-child {
      padding-bottom: 0.2rem;
    }
    p:last-child {
      padding-right: 0.3rem;
      .font(0.24rem, #757575, 400, 0.36rem);
    }
    .more {
      width: 0.4rem;
      height: 0.4rem;
      .van-icon {
        display: block;
        .font(0.4rem, #666, 300, 0.4rem);
        width: 600;
      }
    }
  }
  .van-radio-group {
    margin-bottom: 0.3rem;
    /deep/.van-cell {
      padding: 0.2rem 0.32rem;
      .font(0.26rem, #333, 400, 0.5rem);
      .van-cell__title {
        padding-left: 0.7rem;
        text-align: left;
      }
      .van-radio__icon {
        font-size: 0.4rem;
        line-height: 0.5rem;
        .van-icon {
          font-size: 0.3rem;
          width: 0.4rem;
          height: 0.4rem;
          line-height: 0.4rem;
        }
      }
    }
    .van-cell:first-child .van-cell__title {
      background: url(https://s1.mi.com/m/images/m/micash_wap.png) no-repeat;
      background-size: contain;
    }
    .van-cell:nth-child(2) .van-cell__title {
      background: url(https://s1.mi.com/m/images/m/pay_zfb2.png) no-repeat;
      background-size: contain;
    }
    .van-cell:last-child .van-cell__title {
      background: url(https://s1.mi.com/m/images/m/pay_wx.png) no-repeat;
      background-size: contain;
    }
    .van-cell:not(:last-child)::after {
      left: 0.32rem;
      right: 0.32rem;
      // border-bottom: 0.02rem solid #ebedf0;
    }
    /deep/.van-cell--clickable:active {
      background: transparent;
    }
    /deep/.van-radio__icon--checked .van-icon {
      background-color: @mainColor;
      border-color: @mainColor;
    }
  }
  .van-collapse {
    background: #fff;
    margin-bottom: 0.3rem;
    .van-collapse-item {
      box-sizing: border-box;
      text-align: left;
      /deep/.van-cell {
        background: #fff;
        padding: 0;
        box-sizing: border-box;
        align-items: center;
        padding: 0 0.32rem;
        .font(0.26rem, #333, 400, 0.8rem);
        .van-icon {
          display: block;
          width: 0.24rem;
          height: 0.24rem;
          .font(0.24rem, #969799, 400, 0.24rem);
        }
      }
      /deep/.van-collapse-item__wrapper {
        .van-collapse-item__content {
          padding: 0 0.32rem;
          .font(0.26rem, #333, 400, 0.8rem);
          background: #f5f5f5;
        }
      }
      /deep/.van-cell:not(:last-child)::after {
        left: 0;
        // border-bottom: 0.02rem solid #ebedf0;
      }
      /deep/.van-cell__right-icon {
        margin-left: 0.1rem;
        // height: 0.24rem;
      }
      /deep/.van-collapse-item__title--disabled {
        .van-icon {
          display: none;
        }
      }
      .time {
        border-bottom: 0.02rem solid #eee;
        margin-bottom: 0.24rem;
      }
      .time-list {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        li {
          border: 0.02rem solid #e0e0e0;
          padding: 0 0.4rem;
          background: #fff;
          margin-right: 0.24rem;
          margin-bottom: 0.24rem;
        }
        li.active {
          border-color: @mainColor;
        }
      }
    }
  }
  .payinfo {
    width: 100%;
    position: fixed;
    bottom: 0;
    height: 1.54rem;
    background: #fff;
    .address {
      background: #fbf3c4;
      padding: 0 0.2rem;
      .font(0.26rem, #b57842, 400, 0.54rem);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    div {
      display: flex;
      justify-content: space-between;
      .price {
        flex: 1;
        .font(0.32rem, @mainColor, 400, 1rem);
        span {
          font-weight: 600;
        }
      }
      .btn {
        flex: 1;
        .font(0.32rem, #fff, 400, 1rem);
        background: @mainColor;
      }
    }
  }
  .products {
    background: #fff;
    margin-bottom: 0.3rem;
    li {
      display: flex;
      justify-content: space-between;
      margin: 0 0.32rem;
      padding: 0.16rem 0;
      .font(0.26rem, #3c3c3c, 400, 0.8rem);
      border-bottom: 1px solid #ebedf0;
      img {
        width: 0.8rem;
        height: 0.8rem;
        margin-right: 0.2rem;
      }
      p {
        flex: 1;
        text-align: left;
        span {
          padding-right: 0.2rem;
          font-weight: normal;
        }
      }
      span {
        font-weight: 600;
      }
    }
    li:last-child {
      border: none;
    }
  }
  .detail {
    background: #fff;
    text-align: left;
    margin-bottom: 1.8rem;
    padding: 0.16rem 0;
    li {
      padding-left: 0.32rem;
      .font(0.26rem, #3c3c3c, 400, 0.5rem);
      span {
        font-weight: 600;
      }
    }
  }
}
</style>