<template>
  <van-nav-bar :title="title" left-arrow @click-left="goBack">
    <van-icon name="search" slot="right" />
  </van-nav-bar>
</template>

<script type="text/javascript">
import { Icon, NavBar } from "vant";
export default {
  props: {
    title: {
      type: String,
      default: "标题"
    }
  },
  data() {
    return {};
  },
  components: {
    vanIcon: Icon,
    vanNavBar: NavBar
  },
  computed: {
    getTitle: {
      get: function() {
        return this.title;
      },
      set: function(val) {
        this.emit("update:title", val);
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less" scoped>
.van-nav-bar {
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  background: #f2f2f2;
  position: fixed;
  top: 0;
  .van-nav-bar__title {
    font-size: 0.32rem;
  }
  .van-icon {
    font-size: 0.4rem;
    color: #999;
  }
}
</style>