<template>
  <van-tabbar v-model="getActive" active-color="#ff6034">
    <van-tabbar-item to="/">
      <van-icon name="wap-home" />
      <span>首页</span>
    </van-tabbar-item>
    <van-tabbar-item to="/classify">
      <van-icon name="label" />
      <span>分类</span>
    </van-tabbar-item>
    <van-tabbar-item to="/shopcar">
      <van-icon name="cart" />
      <span>购物车</span>
    </van-tabbar-item>
    <van-tabbar-item to="/my">
      <van-icon name="manager" />
      <span>我的</span>
    </van-tabbar-item>
  </van-tabbar>
</template>

<script type="text/javascript">
import { Icon, Tabbar, TabbarItem } from "vant";
export default {
  props: {
    active: { type: Number, default: 0 }
  },
  data() {
    return {};
  },
  components: {
    vanIcon: Icon,
    vanTabbar: Tabbar,
    vanTabbarItem: TabbarItem
  },
  computed: {
    getActive: {
      get: function() {
        return this.active;
      },
      set: function(val) {
        this.$emit("update:active", val);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.van-tabbar {
  height: 1rem;
  z-index: 10 !important;
}
/deep/ .van-tabbar-item {
  display: flex;
  flex-direction: column;
}
/deep/ .van-icon {
  display: block;
  padding-bottom: 0.08rem;
  font-size: 0.44rem;
}
/deep/ .van-tabbar-item__text {
  font-size: 0.24rem;
}
</style>