<template>
  <div class="home">
    <div class="home-title-bar">
      <div class="logo">
        <img
          src="data:image/png;base64,"
        />
      </div>
      <div class="search">
        <van-icon name="search" />
        <span>搜索商品名称</span>
      </div>
      <div class="user">
        <van-icon name="manager-o" />
      </div>
    </div>

    <van-swipe>
      <van-swipe-item v-for="(imgUrl,index) in banner" :key="index">
        <img :src="imgUrl" />
      </van-swipe-item>
    </van-swipe>

    <ul class="icon">
      <li v-for="(icon,index) in iconRegion" :key="index">
        <img :src="icon" />
      </li>
    </ul>

    <div class="modular">
      <div class="modular-item" v-for="(item,index) in modular" :key="index">
        <img :src="item.imgUrl" @click="goChoice" />
        <div class="products">
          <div class="product-item" v-for="(product,index) in item.list" :key="index">
            <img :src="product.imgUrl" />
            <div class="product-con">
              <h3>{{product.productName}}</h3>
              <p class="des">{{product.productDes}}</p>
              <p class="price">
                <span v-if="product.newPrice" class="new-price">￥{{product.newPrice}}起</span>
                <span v-if="product.oldPrice" class="old-price">￥{{product.oldPrice}}</span>
              </p>
              <div class="buybtn" @click="addToCart(product)">加入购物车</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <p class="line">
      <span>我是有底线的</span>
    </p>
    <tab-bar :active="active"></tab-bar>
  </div>
</template>

<script type="text/javascript">
import TabBar from "@/components/TabBar";
import { Icon, Swipe, SwipeItem } from "vant";
import { sayHello } from "@/utils/index.js";
export default {
  data() {
    return {
      active: 0,
      banner: [],
      iconRegion: [],
      modular: []
    };
  },
  components: {
    TabBar,
    vanIcon: Icon,
    vanSwipe: Swipe,
    vanSwipeItem: SwipeItem
  },
  methods: {
    getHomepage() {
      this.$axios.get("data/homepage.json").then(
        res => {
          const { banner, iconRegion } = res.data;
          Object.assign(this, { banner, iconRegion });
        },
        res => {
          console.log("数据获取失败");
        }
      );
    },
    getAllProduct() {
      this.$axios.get("data/allproduct.json").then(
        res => {
          this.modular = res.data.modular;
        },
        res => {
          console.log("数据获取失败");
        }
      );
    },
    goChoice() {
      this.$router.push("/choice");
    },
    addToCart(val) {
      this.$store.commit("addToCart", val);
    }
  },
  mounted() {
    this.getHomepage();
    this.getAllProduct();
    this.$store.commit("setCartInfo");
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";
.home {
  .home-title-bar {
    width: 100vw;
    height: 0.84rem;
    background: #f2f2f2;
    box-shadow: 0 0.04rem 0.08rem rgba(0, 0, 0, 0.1);
    position: fixed;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    .logo {
      width: 0.6rem;
      height: 100%;
      margin: 0 0.2rem;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    .search {
      flex: 1;
      height: 0.6rem;
      margin: 0.12rem 0;
      border: 1px solid #e5e5e5;
      background: #fff;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .van-icon {
        padding-left: 0.12rem;
        display: block;
        width: 0.36rem;
        height: 0.36rem;
      }
      .van-icon-search:before {
        display: block;
        font-size: 0.36rem;
        color: #9e9e9e;
      }
      span {
        font-size: 0.28rem;
        color: #9e9e9e;
        line-height: 0.6rem;
        padding-left: 0.12rem;
      }
    }
    .user {
      width: 0.6rem;
      height: 100%;
      margin: 0 0.2rem;
      display: flex;
      align-items: center;
      .van-icon {
        display: block;
        width: 0.6rem;
        height: 0.6rem;
      }
      .van-icon-manager-o::before {
        display: block;
        width: 0.6rem;
        line-height: 0.6rem;
        font-size: 0.4rem;
        text-align: center;
        color: #6e6e6e;
      }
    }
  }
  .van-swipe {
    padding-top: 0.84rem;
    width: 100vw;
    height: 50vw;
    img {
      width: 100vw;
    }
    /deep/.van-swipe__indicators {
      bottom: 0.24rem;
    }
    /deep/.van-swipe__indicator {
      width: 0.12rem;
      height: 0.12rem;
    }
    /deep/ .van-swipe__indicator--active {
      background-color: #fff;
    }
    /deep/.van-swipe__indicator:not(:last-child) {
      margin-right: 0.12rem;
    }
  }

  .icon {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 20vw;
      img {
        display: block;
        width: 100%;
      }
    }
  }

  .modular {
    width: 100vw;
    .modular-item {
      > img {
        display: block;
        padding-top: 0.2rem;
        background: #e9e9e9;
        width: 100vw;
      }
      .products {
        width: 100vw;
        box-sizing: border-box;
        padding: 0 0.12rem;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .product-item {
          width: 50%;
          box-sizing: border-box;
          > img {
            display: block;
            margin-top: 0.12rem;
            width: 100%;
          }
          .product-con {
            box-sizing: border-box;
            width: 100%;
            padding: 0.12rem 0.16rem;
            text-align: center;
            h3 {
              .font(0.28rem, #333, 400, 0.28rem);
            }
            .des {
              padding-top: 0.12rem;
              .font(0.24rem, #9e9e9e, 400, 0.24rem);
            }
            .price {
              padding-top: 0.12rem;
              .font(0.28rem, #9e9e9e, 400, 0.32rem);
              .new-price {
                color: @mainColor;
              }
              .old-price {
                padding-left: 0.12rem;
                font-size: 0.24rem;
                text-decoration: line-through;
              }
            }
            .buybtn {
              width: 2rem;
              margin: 0.12rem auto;
              background: #ea625b;
              text-align: center;
              .font(0.24rem, #fff, 700, 0.56rem);
              border-radius: 0.06rem;
            }
          }
        }
        .product-item:nth-child(2n-1) {
          padding-right: 0.1rem;
        }
        .product-item:nth-child(2n) {
          padding-left: 0.1rem;
        }
      }
    }
  }
  .line {
    .font(0.24rem, #666, 400, 0.8rem);
    padding-bottom: 1.2rem;
    span {
      margin: 0 auto;
      position: relative;
    }
    span::before,
    span::after {
      content: "";
      display: block;
      width: 0.4rem;
      height: 1px;
      background: #666;
      position: absolute;
      top: 50%;
    }
    span::before {
      left: -0.6rem;
    }
    span::after {
      right: -0.6rem;
    }
  }
}
</style>