<template>
  <div class="login">
    <back-bar :title="title"></back-bar>
    <van-cell-group>
      <van-field
        v-model="username"
        required
        clearable
        type="text"
        label="手机号"
        maxlength="11"
        left-icon="contact"
        placeholder="请输入手机号"
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        placeholder="请输入密码"
        left-icon="bag-o"
        required
        clearable
      />
    </van-cell-group>
    <van-button
      type="primary"
      size="normal"
      :disabled="!getFlag"
      @click="submitForm"
      class="login-btn"
    >登录</van-button>
    <van-button plain type="primary" class="register-btn" @click="goRegister">注册</van-button>
  </div>
</template>

<script type="text/javascript">
import BackBar from "@/components/BackBar";
import { CellGroup, Field, Button, Toast } from "vant";
export default {
  data() {
    return {
      title: "登录",
      username: "",
      password: ""
    };
  },
  components: {
    BackBar,
    vanCellGroup: CellGroup,
    vanField: Field,
    vanButton: Button
  },
  computed: {
    getFlag() {
      const { username, password } = this;
      return username !== "" && password !== "";
    }
  },
  methods: {
    testUser() {
      let reg = /^1[345678][0-9]{9}$/.test(this.username);
      if (!reg) {
        Toast("手机号格式不对");
      } else {
        return true;
      }
    },
    testPass() {
      return true;
    },
    submitForm() {
      if (this.testUser() && this.testPass()) {
        this.common.getUserInfo(this);
        const { userInfo } = this.$store.state;
        const user = userInfo.filter(item => {
          return item.name == this.username;
        })[0];
        if (user) {
          if (user.pass == this.password) {
            // 存储登录用户
            this.common.storeLoginUser(this, user.name);
            //登录成功
            this.$router.push("/my");
          } else {
            Toast("用户名或密码错误");
          }
        } else {
          Toast("该手机号还未注册");
        }
      }
      // const { username, password, userTip, passTip } = this;
      // if (username && password && !userTip && !passTip) {
      //   const { userInfo } = this.$store.state;
      //   const flag = userInfo.some(({ username, password }) => {
      //     return this.username == username && this.password == password;
      //   });
      //   if (flag) {
      //     this.$router.push("/my");
      //   } else {
      //     Toast("用户名或密码错误");
      //   }
      // }
    },
    goRegister() {
      this.$router.push("/register");
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/style/common.less";

.login {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  .van-cell-group {
    margin-top: 0.92rem;
  }
}
.van-field::before {
  content: "";
}
/deep/.van-field__left-icon .van-icon {
  font-size: 0.32rem;
}
/deep/.van-cell__value {
  position: relative;
  .van-field__error-message {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
    font-size: 0.28rem;
  }
}
.van-cell {
  font-size: 0.28rem;
  line-height: 0.88rem;
  /deep/.van-field__label {
    width: 1.2rem;
    text-align: left;
  }
}
.login-btn,
.register-btn {
  font-size: 0.28rem;
  width: 6.8rem;
  height: 0.88rem;
  background-color: @mainColor;
  border-color: @mainColor;
  top: 6rem;
}
.register-btn {
  background-color: #fff;
  border: none;
}
.van-button--plain.van-button--primary {
  color: @mainColor;
}
.van-button::before {
  background-color: transparent;
}
/deep/.van-icon-clear {
  font-size: 0.28rem;
}
</style>